import type { ClientSDK, WixNamespace } from '../types';

type ArgPredicate = (...args: unknown[]) => boolean;

type Respond = (...args: unknown[]) => unknown;

interface ClientSdkMethodStub {
  argPredicate: ArgPredicate;
  respond: Respond;
}

interface ResponseResolver {
  thenResolveWith(response: unknown): ClientSdkMockBuilder;
  thenResolveBy(respond: Respond): ClientSdkMockBuilder;
}

interface ClientSdkMockBuilder {
  when(
    namespace: WixNamespace,
    method: string,
  ): {
    withArgs(...args: unknown[]): ResponseResolver;
    withAny(): ResponseResolver;
  };
  build(): ClientSDK;
}

export const clientSdkMockBuilder = () => {
  const stubs = new Map<WixNamespace, Map<string, ClientSdkMethodStub>>();

  const builder: ClientSdkMockBuilder = {
    when(namespace: WixNamespace, method: string) {
      if (!stubs.has(namespace)) {
        stubs.set(namespace, new Map());
      }

      const buildResponseResolver = (argPredicate: ArgPredicate) => {
        return {
          thenResolveWith(response: unknown) {
            const stub: ClientSdkMethodStub = {
              argPredicate,
              respond: () => response,
            };

            stubs.get(namespace)!.set(method, stub);
            return builder;
          },
          thenResolveBy(respond: Respond) {
            const stub: ClientSdkMethodStub = {
              argPredicate,
              respond,
            };

            stubs.get(namespace)!.set(method, stub);
            return builder;
          },
        };
      };

      return {
        withArgs(...args: unknown[]) {
          const predicate: ArgPredicate = (...callArgs) => {
            return args.every((arg, i) => arg === callArgs[i]);
          };

          return buildResponseResolver(predicate);
        },
        withAny() {
          const alwaysTruePredicate = () => true;
          return buildResponseResolver(alwaysTruePredicate);
        },
      };
    },
    build(): ClientSDK {
      return {
        invoke: async ({ namespace, method, args }) => {
          const stub = stubs.get(namespace)?.get(method);
          if (!stub) {
            throw new Error(
              `No stub found for ${namespace}.${method}(${JSON.stringify(
                args,
              )})`,
            );
          }

          const response = stub.respond(...args);
          return response as any;
        },
      };
    },
  };

  return builder;
};

export const setClientSdkGlobal = (clientSdk: ClientSDK) => {
  window.clientSdk = clientSdk;
};
